* {
    margin: 0;
    padding: 0;
}

body {
    align-items: center;
    background-color: #000;
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100vh;
}

h1 {
    margin: 50px;
}

h1, h2 {
    text-align: center;
    padding: 20px;
}

button {
    background-color: #0a0;
    border: none;
    color: #000;
    cursor: pointer;
    font-size: 1.2em;
    min-width: 50px;
}

#settings-container {
    align-items: stretch;
    align-content: center;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}

.settings {
    align-items: center;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.settings * {
    padding: 7px;
    margin: 10px;
}

#break-info,
#session-info {
    border: 3px solid #fff;
    border-radius: 10px;
    padding: 20px;
    margin: 20px;
}

#timer {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 20px;
}

#time-left {
    font-size: 2em;
    padding: 20px;
}

#timer-actions {
    align-items: stretch;
    align-content: center;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

#timer-actions button {
    padding: 7px;
    margin: 7px;
    font-size: 2em;
    min-width: 50px;
}

#start_stop {
    font-size: 4em !important;
}